import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router-dom';
import '../App.css';
import DatePicker, {registerLocale} from 'react-datepicker';
import firebase from '../Firebase';
import placa from '../image/placa.png';
import cliente from '../image/client.png';
import correo from '../image/email.png';
//import * as firebase from 'firebase/app';
//import 'firebase/auth';
//import 'firebase/firestore';

import "react-datepicker/dist/react-datepicker.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import es from 'date-fns/locale/es';
registerLocale("es", es);

class Create extends Component {

  constructor (props) {
    const options = {
      weekday: "long", 
      year: "numeric", 
      month: "long", 
      day: "numeric", 
      hour: "numeric", 
      minute: "numeric", 
      hour12:"false"};
      
    super(props)
    this.ref = firebase.firestore().collection('clients');
    this.state = {
    idClient: '',
    numberPlate: '',
    dateFormatString: new Date().toLocaleString("es-Co", options),
    name: '',
    email: '',
    dateFormatDate: new Date(),
    startDate: ''    
    };
    this.handleChange = this.handleChange.bind(this);    
    this.unsubscribe = null;    
    }
  
    onCollectionUpdate = (querySnapshot) => {
      const clients = [];
      const maxNumber = [];
      querySnapshot.forEach((doc) => {
        const { idClient, numberPlate, name, email } = doc.data();
        clients.push({
          key: doc.id,
          doc, // DocumentSnapshot
          idClient,
          numberPlate,
          name,
          email
        });
        maxNumber.push({
          idClient
        });  
      });
      this.setState({
        clients,
        idClient: Math.max.apply(Math, maxNumber.map(function(o) { return o.idClient; }))+1
     });
    }
  
    componentDidMount() {
      this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate); 
    }
  
  handleChange(date) {
    var options = {
      weekday: "long", 
      year: "numeric", 
      month: "long", 
      day: "numeric", 
      hour: "numeric", 
      minute: "numeric", 
      hour12:"false"};
    this.setState({        
    startDate: date,    
    dateFormatString: date.toLocaleString("es-Co", options),
    dateFormatDate: date
    })
  }

  onChangeOnlyLetters = (e) => {      
    const state = this.state
    state[e.target.name] = e.target.value;
    state[e.target.name] = state[e.target.name].replace(/[^A-Za-zÀ-ÿ]/ig, ' ')
    this.setState(state);    
  }
  
  onChange = (e) => {      
    const state = this.state
    state[e.target.name] = e.target.value;    
    this.setState(state);
  }

  onChangeUpperCase = (e) => {
    const state = this.state
    state[e.target.name] = e.target.value.toUpperCase();
    this.setState(state);    
    if (e.target.textLength === 6){
      this.state.clients.map(Coment => 
        {if (e.target.value.toUpperCase() === Coment.numberPlate){
          alert("La placa del vehiculo ya fue registrada!")
          state[e.target.name] = "";
          this.setState(state);         
        }}      
      )
    }
       
  }

  onChangeLowerCase = (e) => {
    const state = this.state
    state[e.target.name] = e.target.value.toLowerCase(); 
    this.setState(state);
  }

  onSubmit = (e) => {
    e.preventDefault();

    const { idClient, numberPlate, name, email, dateFormatString, dateFormatDate } = this.state;

    this.ref.add({
      idClient,
      numberPlate,      
      name,
      email,
      dateFormatString,
      dateFormatDate
    }).then((docRef) => {
      this.setState({
        idClient: '',
        numberPlate: '',        
        name: '',
        email: '',
        dateFormatString: '',
        dateFormatDate: ''
      });
      //this.props.history.push("/create")
      window.location.reload(true);
    })
    .catch((error) => {
      console.error("Error adding document: ", error);
    });
    alert("Correo Registrado Exitosamente!!")
  }
  
  render() {
    const { numberPlate, name, email } = this.state;
    return (
      <div class="container">
        <div class="panel panel-default">
          <div class="panel-heading text-center panel-relative">
            <h3 class="panel-title">
              AGREGAR REGISTRO DE CLIENTE
            </h3>
          </div>
          <div class="panel-body">
            <form onSubmit={this.onSubmit}>
              <div class="form-group">                
                <label for="numberPlate">Placa del Vehiculo:</label>
                &nbsp;
                <img src={placa} />
                <input
                  type="text" 
                  class="form-control"
                  name="numberPlate"
                  value={numberPlate} 
                  onChange={this.onChangeUpperCase}
                  placeholder="Ingrese la Placa"
                  minlength="6"
                  maxlength="6"
                  data-toggle="tooltip"
                  title="Ingresa la Placa de su Vehiculo"
                  required
                  />
              </div>    
              <div class="form-group">
                <label for="name">Nombre del Cliente:</label>
                &nbsp;
                <img src={cliente} />               
                <input
                  type="text" 
                  class="form-control"
                  name="name" 
                  value={name} 
                  onChange={this.onChangeOnlyLetters} 
                  placeholder="Ingrese su Nombre"
                  minlength="7"
                  maxlength="50"
                  data-toggle="tooltip"
                  title="Ingresa su Nombre"
                  required
                  />
              </div>
              <div class="form-group">
                <label for="email">Correo Electronico:</label>
                &nbsp;
                <img src={correo} />
                <input
                  type="text" 
                  class="form-control"
                  name="email" 
                  value={email} 
                  onChange={this.onChangeLowerCase} 
                  placeholder="Ingrese su Correo Electronico"
                  minlength="13"
                  maxlength="50"
                  data-toggle="tooltip"
                  title="Ingresa su Correo Electronico"
                  required
                  />
              </div>              
              <button name="btnSubmit" type="submit" class="btn btn-success">Aceptar</button>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default Create;
